import { LOCATION } from '../utils';
import { Events, FluxCapacitor } from 'groupby-api';

export class Redirect {

  constructor(private flux: FluxCapacitor) { }

  init() {
    this.flux.on(Events.REDIRECT, (redirect) => LOCATION.assign(redirect));
  }
}



